import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-blog-post',
  templateUrl: './blog-post.component.html',
  styleUrls: ['./blog-post.component.scss'],
})
export class BlogPostComponent implements OnInit {
  title: any;
  img: any;
  author: any;
  exactDate: any;
  exactTime: any;
  body: any;
  authorFB: any;
  name: any;
  email: any;
  fullComment: any;
  comment: any;
  allcomment: any;
  commented = false;
  invalidEmail = false;
  validEmail = false;
  commentIT() {
    const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (!re.test(this.email)) {
      this.validEmail = false;
    } else {
      this.validEmail = true;
    }
    if (this.validEmail) {
      this.comment = {
        n: this.name,
        e: this.email,
        c: this.fullComment,
        t: this.title,
        a: this.author,
        date: new Date().getTime(),
      };
      this.db.collection('comments').add(this.comment);
      this.commented = true;
    } else {
      this.invalidEmail = true;
    }
  }
  constructor(public route2: ActivatedRoute, public db: AngularFirestore) {
    db.collection('comments', (res) => res.orderBy('date', 'desc'))
      .snapshotChanges()
      .subscribe((res) => (this.allcomment = res));
  }
  ngOnInit(): void {
    let xoxo = this.route2.snapshot.paramMap.get('data');
    let x = xoxo?.split('--:--');
    this.title = x?.[0];
    this.img = x?.[1];
    this.author = x?.[2];
    this.authorFB = x?.[3];
    this.exactDate = x?.[4];
    this.exactTime = x?.[5];
    this.body = x?.[6];
  }
}
