import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-videos',
  templateUrl: './videos.component.html',
  styleUrls: ['./videos.component.scss'],
})
export class VideosComponent implements OnInit {
  youtube =
    'https://www.facebook.com/sharer/sharer.php?u=https%3A%2F%2Fyoutu.be%2F';
  sparse = '&amp;src=sdkpreparse';
  embed = 'https://www.youtube.com/embed/';
  videos: any;
  constructor(public db: AngularFirestore, public sanitizer: DomSanitizer) {
    db.collection('videos', (res) => res.orderBy('date', 'desc'))
      .snapshotChanges()
      .subscribe((res) => (this.videos = res));
  }

  ngOnInit(): void {}
}
